
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
nav {
  height: 10vh;
  background-color: pink;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
}
nav a{
  padding: 1em;
  color: rgb(42, 5, 51);
  text-decoration: none;
  font-size: 1.5em;
}
@media only screen and (max-width: 580px) {
nav a {
		display: none;
}
nav a#main-menu-item {
		display: block;
}
}
#container{
  width: 100vw;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#basicCalc {
    width: 75vw;
    border: 1px solid rgb(54, 10, 63);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 1em;
}
.display{
    padding: 1em;
    border: 1px solid rgb(54, 10, 63);
    width: 70%;
	overflow: hidden;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	text-align: right;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-size: 1.5em;
}
#buttons {
    display: grid;
	grid-template-columns: repeat(4, 1fr);
	width: 70%;
}
#buttons > * {
    border: 1px solid grey;
    border-radius: 10px;
    cursor: default;
    text-align: center;
    padding: 1em 0.5em;
    margin: 0.5em;
    -webkit-box-shadow: 2px 2px 4px 1px rgba(128, 128, 128, 0.472);
            box-shadow: 2px 2px 4px 1px rgba(128, 128, 128, 0.472);
}
.button:hover{
    background-color: rgb(48, 48, 82);
    color: white;
}
div#buttons > *:active {
    background-color: rgb(19, 19, 47);
    color: white;
    -webkit-box-shadow: none;
            box-shadow: none;
}
@media only screen and (max-width: 580px) {
div#buttons {
		grid-template-columns: repeat(4, 1fr);
		text-align: center;
		width: 100%;
}
div#basicCalc {
		border: none;
		font-weight: bold;
		font-size: 1.3em;
		width: 100%;
		padding: 0;
}
div.display {
		width: 100%;
		height: 100%;
		padding:1em;
		border: none;
		border-bottom: 1px solid grey;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
div#buttons > *{
		border: none;
		box-shadow: none;
		-webkit-box-shadow: none;
}
}



























































































































































































